* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
body {
  background-color: rgb(255, 246, 255);
}
.list {
  max-width: 640px;
  width: calc(100%- 20px);
  margin: 20px auto;
  background-color: rgb(228, 240, 255);
  text-align: center;
  position: relative;
}
h2 {
  padding: 20px 0;
}
input {
  width: 300px;
  height: 40px;
  border: 0;
  border-radius: 50px;
  padding-left: 20px;
  margin: 20px 0;
}
button {
  margin: 0 10px;
  width: 80px;
  height: 40px;
  border: 0;
  background-color: teal;
  color: #fff;
  border-radius: 50px;
}
button:hover {
  background-color: #333;
  transition: 1.5s;
}
li {
  margin-bottom: 10px;
}
span::before {
  content: '‚ùé';
  float: right;
  margin-right: 100px;
}
